.button {
  border: none;
  padding: 1rem 2rem;
  min-width: 100px;
  font-weight: 500;
  font-size: 24px;
  line-height: 130%;
  color: var(--color-text);
  background-color: var(--color-main);
  transition: background-color 0.3s, box-shadow 0.3s;
  cursor: pointer;
}

.button:hover {
  box-shadow: 0 12px 25px rgba(78, 101, 52, 0.25);
  background-color: #97c166;
  transition: background-color 0.3s, box-shadow 0.3s;
}

.button:active {
  background-color: #7d9d58;
  transition: background-color 0.3s, box-shadow 0.3s;
}

@media (max-width: 1000px) {
  .button {
    padding: 16px;
    font-size: 20px;
  }
}

html {
  box-sizing: border-box;
  font-size: 16px;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  font-family: "Oswald", sans-serif;
  color: var(--color-text);
  background-color: var(--color-bg);
}

img {
  max-width: 100%;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
  padding: 0;
}

.is-outer {
  position: relative !important;
  left: -9999px !important;
}

.site-container {
  overflow: hidden;
}

.stop-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
}

.is-hidden {
  display: none !important;
}

.for-mobile {
  display: none;
}

@media (max-width: 1024px) {
  .for-mobile {
    display: block;
  }
}

.error {
  border: 2px #ff4019 solid !important;
}

.visually-hidden {
  position: absolute !important;
  clip: rect(1px 1px 1px 1px);
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0 !important;
  border: 0 !important;
  height: 1px !important;
  width: 1px !important;
  overflow: hidden;
}

.btn-reset {
  border: none;
  background: transparent;
  cursor: pointer;
}

.container {
  margin: 0 auto;
  padding: 0 15px;
  max-width: 1170px;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

