.contacts-item a:hover::after {
  opacity: 1;
  transition: opacity 0.2s ease;
}

.contacts-item__icon {
  margin-right: 8px;
  width: 20px;
  height: 20px;
}

@media (max-width: 320px) {
  .contacts-item__icon {
    width: 18px;
    height: 18px;
  }
}

.contacts-item__link {
  position: relative;
  margin-bottom: 8px;
  color: var(--color-text);
}

@media (max-width: 425px) {
  .contacts-item {
    font-size: 14px;
  }
}

@media (max-width: 320px) {
  .contacts-item {
    font-size: 12px;
  }
}

.social {
  display: flex;
  flex-direction: column;
}

.social__link {
  display: flex;
  align-items: center;
  margin-bottom: 16px;
}

.social__link:nth-child(3) {
  margin-bottom: 0;
}

.social__icon {
  margin-right: 8px;
  width: 24px;
  height: 24px;
}

@media (max-width: 320px) {
  .social__icon {
    margin-right: 4px;
    width: 20px;
    height: 20px;
  }
}

.social__name {
  position: relative;
  font-family: "Lato";
  font-weight: 400;
  font-size: 14px;
  line-height: 17px;
  color: var(--color-text);
}

.social__name::after {
  content: "";
  position: absolute;
  left: 0;
  bottom: -2px;
  width: 100%;
  height: 2px;
  background-color: var(--color-text);
  opacity: 0;
  transition: opacity 0.2s linear;
}

.social__name:hover::after {
  opacity: 1;
  transition: opacity 0.2s ease;
}

@media (max-width: 320px) {
  .social__name {
    font-size: 12px;
  }
}

@media (max-width: 655px) {
  .social {
    width: 50%;
  }
}

@media (max-width: 425px) {
  .social {
    align-self: center;
    width: auto;
  }
}

@media (max-width: 320px) {
  .social {
    font-size: 12px;
  }
}

.modal {
  position: fixed;
  left: 0;
  top: 0;
  z-index: 1050;
  display: none;
  overflow: hidden;
  padding: 0 80px;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
}

@media (max-width: 600px) {
  .modal {
    padding: 0 40px;
  }
}

@media (max-width: 425px) {
  .modal {
    padding: 0;
  }
}

.modal__dialog {
  margin: 40px auto;
  border: 10px solid var(--color-main);
  max-width: 1000px;
}

@media (max-width: 425px) {
  .modal__dialog {
    border-width: 5px;
  }
}

.modal__img {
  height: auto;
  max-width: 465px;
  object-fit: cover;
}

@media (max-width: 1000px) {
  .modal__img {
    width: 40%;
  }
}

@media (max-width: 768px) {
  .modal__img {
    display: none;
  }
}

.modal__content {
  position: relative;
  display: flex;
  overflow-y: auto;
  width: 100%;
  max-height: 80vh;
  background-color: #fff;
}

.modal__form {
  display: flex;
  flex-direction: column;
  margin: 55px 30px 55px 35px;
  width: 100%;
}

@media (max-width: 425px) {
  .modal__form {
    margin: 25px 10px;
  }
}

.modal__close {
  position: absolute;
  right: 14px;
  top: 8px;
  border: none;
  font-weight: 700;
  font-size: 30px;
  color: #000;
  background-color: transparent;
  opacity: 0.5;
  cursor: pointer;
}

.modal__title {
  margin-bottom: 65px;
  font-family: "Oswald";
  font-weight: 400;
  font-size: 24px;
  line-height: 36px;
  text-align: center;
}

@media (max-width: 425px) {
  .modal__title {
    margin-bottom: 32px;
  }
}

.modal__input {
  display: block;
  margin-bottom: 1.5rem;
  border: 3px solid var(--color-main);
  padding-left: 12px;
  height: 50px;
  font-weight: 400;
  font-size: 18px;
  background: inherit;
}

.modal__input::placeholder {
  color: rgba(26, 33, 18, 0.4);
}

.modal .btn {
  display: block;
  margin: 0 auto;
  width: 280px;
}

.show {
  display: block;
}

.show .modal__dialog {
  animation-name: fade;
  animation-duration: 0.5s;
}

.hide {
  display: none;
}

.fade {
  animation-name: fade;
  animation-duration: 1.5s;
}

@keyframes fade {
  from {
    opacity: 0.1;
    transform: translate(0, 40px);
  }
  to {
    opacity: 1;
    transform: translate(0, 0);
  }
}
